.cart{
  right: 0;
  top: 4.5em;
  padding: 0;
  margin: 0;
  position:absolute;
  background-color: #fff;
  box-shadow: 0px 0 3px black;
  list-style: none;
}



.cartButton{
  margin-left: auto;
  padding: 15px;
  background-color: #e77e1c;
  font-size: 1.2em;
  color: #fff;
  font-weight: bold;
  border: 0;
  cursor: pointer;
}

@media (max-width: 540px) { 
  .cartButton{
    right: 0;
    top: 0;
    position: fixed;
  }

  .cart {
    z-index: 2;
    top: 4em;
    position: fixed;
  }
}